/**
 * Conf file
 */

export default class Conf {
    CONF = {
        //  []
        "requestDevelopmentName": "",

        //  []
        "requestProductionName": "",

        "requestTestName": "",

        // 
        "child": {}
    };


    // 
    async initConf() {
        let confs = await import('/public/config/requestConf.json');
        this.CONF = Object.assign(
            this.CONF,
            {...confs}
        );
        return this.CONF;
    }

    get getConf() {
        return this.CONF;
    }
}
