/**
 * 
 */

export default class Time {
    constructor() {
        this.DATE = new Date()
    }

    get nowTimeStamp() {
        return this.DATE.getTime();
    }

    update() {
        this.DATE = new Date()
        return this
    }

    /**
     * 
     */
    get appStart() {
        return 1541260800000;
    }

    get appStartDate() {
        return new Date(this.appStart);
    }
}
