import cheaterStatus from '/public/config/cheaterStatus.json';
import i18n from "../i18n";

const columnNames = cheaterStatus.child.map(i => ({
    value: i.value,
    label: i18n.t(`basic.status.${i.value}.text`)
}));

let data = {
    type: 'container',
    children: generateItems(columnNames.length, i => ({
        id: `column${i}`,
        type: 'container',
        name: columnNames[i],
        children: [],
    }))
};

/**
 * -
 * @param count
 * @param creator
 * @returns {*[]}
 */
function generateItems(count, creator) {
    const result = []
    for (let i = 0; i < count; i++) {
        result.push(creator(i))
    }
    return result
}

export default {
    state: {
        $desktop: {
            // 
            workflow: {
                config: {},                     // 
                data,                           // 
                autoUpdatePlayerLoading: true,  // 
                autoUpdatePlayerList: {         // 
                    result: [],
                    total: 0
                }
            },
        },
    },
}
